import React from 'react';
import Card from './Card';

const CardList = ({upgrades, numberWithCommas, processCardEffect}) => {

    return(
    <div>
        
        {
            upgrades.map((user, i) => {
                return(
                    <Card 
                        key={i} 
                        upgradeId={upgrades[i].id} 
                        name={upgrades[i].name} 
                        cost={upgrades[i].cost} 
                        happinessCost={upgrades[i].happinessCost} 
                        effect={upgrades[i].effect} 
                        clickIncome={upgrades[i].clickIncome} 
                        dollarIncome={upgrades[i].dollarIncome} 
                        dollarTypeIncome={upgrades[i].dollarTypeIncome} 
                        dollarBoost={upgrades[i].dollarBoost} 
                        happinessIncome={upgrades[i].happinessIncome} 
                        happinessBoost={upgrades[i].happinessBoost} 
                        spending={upgrades[i].spending} 
                        annualBonus={upgrades[i].annualBonus} 
                        owned={upgrades[i].owned}processCardEffect
                        numberWithCommas = {numberWithCommas}
                        processCardEffect = {processCardEffect}

                        />
                );
            })
        }
    </div>
    );
}

export default CardList;
