import React from 'react';
import './clickCash.css';

const ClickCash = ({ clickIncome, clickCashOut }) => {


    return (
        <div className='bg-navy pb2'>
            <h1 className='mb0'>Click for Cash!</h1>
            <br></br>
            <label className='yellow'>Buck a Click</label>
            <br></br>
            <button id='buckaclick' className='pa1 f2 grow bw2 shadow-5'  onClick={clickCashOut}>${clickIncome}</button>
        </div>
    );
}

export default ClickCash;