import React from 'react';
import './statbox.css';

const StatsBox = ({ dollarIncome, hoursPassed, daysPassed, yearsPassed, mainHappiness, happinessIncome, spending, annualBonus, numberWithCommas}) => {

    let displayAnnualBonus = numberWithCommas(annualBonus);
    return (
        <div id='statbox' className='statbox light-gray'>
            <p className='statincome light-green'>You earn ${dollarIncome}/hour, 24 hours a day. &#160;&#160;&#160;&#160;Annual Bonus = ${displayAnnualBonus}</p>
            <p className='costliving'>Cost of living: ${spending} / day</p>
            <p className='stathappy'>Happy &#x1F604;: {mainHappiness}. &#160;&#160;&#160;&#160;&#160; You earn &#x1F604;: {happinessIncome} per day.</p> 
            <p className='stathappy'></p> 
            

            <p className='light-yellow'>{Math.round(yearsPassed)} years / {Math.round(daysPassed)} days / {hoursPassed} hours passed</p>


        </div>
    );
}

export default StatsBox;